"use client";
import { useState, useEffect, Suspense } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { useRouter, useSearchParams } from 'next/navigation';
import { motion, AnimatePresence } from 'framer-motion';
import { db } from '@/lib/firebase';
import { collection, addDoc, serverTimestamp } from 'firebase/firestore';

function HomeContent() {
    const [description, setDescription] = useState('');
    const [selectedTheme, setSelectedTheme] = useState('light');
    const [logo, setLogo] = useState(null);
    const [isGenerating, setIsGenerating] = useState(false);
    const [loading, setLoading] = useState(false);
    const router = useRouter();
    const searchParams = useSearchParams();

    const handleLogoUpload = (e) => {
        const file = e.target.files[0];
        if (file) {
            const reader = new FileReader();
            reader.onloadend = () => {
                setLogo(reader.result); // Base64
            };
            reader.readAsDataURL(file);
        }
    };

    const themes = [
        { id: 'light', name: '◊†◊ß◊ô ◊ï◊ë◊î◊ô◊®', icon: '‚òÄÔ∏è' },
        { id: 'dark', name: '◊õ◊î◊î ◊ï◊ô◊ï◊ß◊®◊™◊ô', icon: 'üåô' },
        { id: 'corporate', name: '◊¢◊°◊ß◊ô ◊ï◊û◊ß◊¶◊ï◊¢◊ô', icon: 'üíº' },
        { id: 'cyberpunk', name: '◊†◊ï◊¢◊ñ ◊ï◊ò◊õ◊†◊ï◊ú◊ï◊í◊ô', icon: '‚ö°' }
    ];

    const handleCreate = async () => {
        if (!description.trim()) {
            alert("◊ê◊†◊ê ◊™◊ê◊® ◊ê◊™ ◊î◊¢◊°◊ß ◊©◊ú◊ö ◊ß◊ï◊ì◊ù! ‚ú®");
            return;
        }

        setIsGenerating(true);
        setLoading(true);

        try {
            // 1. ◊©◊û◊ô◊®◊™ ◊î◊†◊™◊ï◊†◊ô◊ù ◊ë-Firestore
            const docRef = await addDoc(collection(db, "sites"), {
                prompt: description,
                theme: selectedTheme,
                logoUrl: logo, // Base64
                createdAt: serverTimestamp(),
                isPaid: false
            });

            // 2. ◊î◊¢◊ë◊®◊î ◊ú◊ì◊£ ◊î◊°◊ô◊û◊ï◊ú◊¶◊ô◊î ◊¢◊ù ◊î-ID ◊î◊ó◊ì◊©
            router.push(`/simulate?id=${docRef.id}`);

        } catch (e) {
            console.error("Error adding document: ", e);
            alert("◊î◊ô◊™◊î ◊©◊í◊ô◊ê◊î ◊ë◊©◊û◊ô◊®◊™ ◊î◊†◊™◊ï◊†◊ô◊ù, ◊†◊°◊î ◊©◊ï◊ë.");
            setIsGenerating(false);
            setLoading(false);
        }
    };

    useEffect(() => {
        const descFromUrl = searchParams.get('desc');
        if (descFromUrl) {
            setTimeout(() => {
                setDescription(descFromUrl);
            }, 0);
            // ◊í◊ú◊ô◊ú◊î ◊ú◊©◊ì◊î ◊î◊ß◊ú◊ò ◊ê◊ù ◊ô◊© ◊§◊®◊û◊ò◊®
            const timer = setTimeout(() => {
                window.scrollTo({ top: 300, behavior: 'smooth' });
            }, 800);
            return () => clearTimeout(timer);
        }
    }, [searchParams]);

    return (
        <div style={{
            minHeight: '100vh',
            background: 'radial-gradient(circle at top, #1e293b 0%, #020617 100%)',
            color: 'white',
            padding: '120px 20px 60px',
            direction: 'rtl'
        }}>

            <section style={{ textAlign: 'center', maxWidth: '900px', margin: '0 auto 80px' }}>
                <div style={{
                    display: 'inline-block',
                    padding: '6px 16px',
                    background: 'rgba(59, 130, 246, 0.1)',
                    borderRadius: '100px',
                    color: '#60a5fa',
                    fontSize: '0.9rem',
                    fontWeight: '600',
                    border: '1px solid rgba(59, 130, 246, 0.2)',
                    marginBottom: '24px'
                }}>
                    ‚ú® ◊î◊ë◊ô◊†◊î ◊î◊û◊ú◊ê◊õ◊ï◊™◊ô◊™ ◊©◊ë◊ï◊†÷∏◊î ◊¢◊ë◊ï◊®◊ö
                </div>

                <h1 style={{
                    fontSize: 'clamp(2.5rem, 8vw, 4.5rem)',
                    fontWeight: '950',
                    lineHeight: '1.1',
                    marginBottom: '20px',
                    background: 'linear-gradient(to bottom, #fff 30%, #94a3b8 100%)',
                    WebkitBackgroundClip: 'text',
                    WebkitTextFillColor: 'transparent'
                }}>
                    ◊ë◊†◊î ◊ê◊™◊® ◊†◊ó◊ô◊™◊î<br />
                    <span style={{ color: '#3b82f6' }}>◊ë◊û◊î◊ô◊®◊ï◊™ ◊î◊ê◊ï◊®.</span>
                </h1>

                <p style={{ fontSize: '1.25rem', color: '#94a3b8', marginBottom: '40px', maxWidth: '600px', margin: '0 auto 40px' }}>
                    ◊î◊ë◊ô◊†◊î ◊î◊û◊ú◊ê◊õ◊ï◊™◊ô◊™ ◊©◊ú◊†◊ï ◊™◊î◊§◊ï◊ö ◊ê◊™ ◊î◊®◊¢◊ô◊ï◊ü ◊©◊ú◊ö ◊ú◊ê◊™◊® ◊ô◊ï◊ß◊®◊™◊ô, ◊û◊î◊ô◊® ◊ï◊û◊ï◊õ◊ü ◊ú◊û◊õ◊ô◊®◊ï◊™ ◊™◊ï◊ö ◊§◊ó◊ï◊™ ◊û-60 ◊©◊†◊ô◊ï◊™.
                </p>

                {/* ◊ë◊ó◊ô◊®◊™ ◊°◊í◊†◊ï◊ü ◊¢◊ô◊¶◊ï◊ë◊ô */}
                <div style={{ display: 'flex', gap: '15px', justifyContent: 'center', marginBottom: '30px', flexWrap: 'wrap' }}>
                    {themes.map((theme) => (
                        <motion.button
                            key={theme.id}
                            whileHover={{ scale: 1.05 }}
                            whileTap={{ scale: 0.95 }}
                            onClick={() => setSelectedTheme(theme.id)}
                            style={{
                                padding: '12px 24px',
                                borderRadius: '16px',
                                border: selectedTheme === theme.id ? '2px solid #3b82f6' : '1px solid rgba(255,255,255,0.1)',
                                background: selectedTheme === theme.id ? 'rgba(59, 130, 246, 0.15)' : 'rgba(30, 41, 59, 0.4)',
                                color: selectedTheme === theme.id ? '#fff' : '#94a3b8',
                                cursor: 'pointer',
                                transition: 'all 0.2s ease',
                                display: 'flex',
                                flexDirection: 'column',
                                alignItems: 'center',
                                gap: '4px',
                                minWidth: '120px'
                            }}
                        >
                            <span style={{ fontSize: '1.5rem' }}>{theme.icon}</span>
                            <span style={{ fontSize: '0.85rem', fontWeight: 'bold' }}>{theme.name}</span>
                        </motion.button>
                    ))}
                </div>

                {/* ◊î◊¢◊ú◊ê◊™ ◊ú◊ï◊í◊ï */}
                <div style={{ marginBottom: '40px', textAlign: 'center' }}>
                    <label style={{ color: '#94a3b8', display: 'block', marginBottom: '12px', fontSize: '0.95rem' }}>◊ô◊© ◊ú◊ö ◊ú◊ï◊í◊ï? (◊ê◊ï◊§◊¶◊ô◊ï◊†◊ú◊ô)</label>
                    <div style={{ position: 'relative', display: 'inline-block' }}>
                        <input
                            type="file"
                            onChange={handleLogoUpload}
                            accept="image/*"
                            style={{
                                display: 'none'
                            }}
                            id="logo-upload"
                        />
                        <label
                            htmlFor="logo-upload"
                            style={{
                                background: 'rgba(255, 255, 255, 0.05)',
                                border: '1px dashed rgba(255, 255, 255, 0.2)',
                                borderRadius: '12px',
                                padding: '12px 24px',
                                color: logo ? '#3b82f6' : '#94a3b8',
                                cursor: 'pointer',
                                display: 'flex',
                                alignItems: 'center',
                                gap: '10px',
                                transition: 'all 0.2s ease',
                                fontSize: '0.9rem'
                            }}
                        >
                            <span>{logo ? "‚úÖ ◊ú◊ï◊í◊ï ◊†◊ë◊ó◊®" : "üìÅ ◊î◊¢◊ú◊ê◊™ ◊ú◊ï◊í◊ï"}</span>
                            {logo && <Image src={logo} alt="logo preview" width={48} height={24} unoptimized style={{ height: '24px', width: 'auto', borderRadius: '4px' }} />}
                        </label>
                    </div>
                </div>

                <div style={{
                    background: 'rgba(30, 41, 59, 0.5)',
                    padding: '8px',
                    borderRadius: '20px',
                    border: '1px solid rgba(255, 255, 255, 0.1)',
                    display: 'flex',
                    maxWidth: '600px',
                    margin: '0 auto',
                    boxShadow: '0 20px 50px rgba(0,0,0,0.5)'
                }}>
                    <input
                        type="text"
                        placeholder="◊™◊ê◊® ◊ê◊™ ◊î◊¢◊°◊ß ◊©◊ú◊ö (◊ú◊û◊©◊ú: ◊û◊ê◊û◊ü ◊õ◊ï◊©◊® ◊ë◊ó◊ô◊§◊î...)"
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                        style={{
                            flex: 1, background: 'transparent', border: 'none', color: 'white',
                            padding: '15px 20px', fontSize: '1.1rem', outline: 'none',
                            textAlign: 'right'
                        }}
                    />
                    <motion.button
                        whileHover={{ scale: 1.02, boxShadow: "0 20px 40px rgba(59, 130, 246, 0.4)" }}
                        whileTap={{ scale: 0.98 }}
                        onClick={handleCreate}
                        style={{
                            position: 'relative',
                            background: 'linear-gradient(135deg, #3b82f6 0%, #2563eb 100%)',
                            color: '#fff',
                            padding: '12px 35px',
                            borderRadius: '14px',
                            fontSize: '1.2rem',
                            fontWeight: '900',
                            border: 'none',
                            cursor: 'pointer',
                            overflow: 'visible',
                            display: 'flex',
                            alignItems: 'center',
                            gap: '12px'
                        }}
                    >
                        <AnimatePresence>
                            {isGenerating && (
                                <>
                                    {[...Array(15)].map((_, i) => (
                                        <motion.span
                                            key={i}
                                            initial={{ opacity: 1, scale: 0, x: 0, y: 0 }}
                                            animate={{
                                                opacity: 0,
                                                scale: [0, 2, 0],
                                                x: (i % 2 === 0 ? 1 : -1) * (i * 20),
                                                y: (i % 3 === 0 ? 1 : -1) * (i * 15),
                                                rotate: i * 24
                                            }}
                                            transition={{ duration: 1, ease: "easeOut" }}
                                            style={{ position: 'absolute', fontSize: '1.8rem', pointerEvents: 'none' }}
                                        >
                                            ‚ú®
                                        </motion.span>
                                    ))}
                                </>
                            )}
                        </AnimatePresence>

                        <span style={{ fontSize: '1.8rem' }}>{isGenerating ? "ü™Ñ" : "‚ú®"}</span>
                        {isGenerating ? "◊ë◊ï◊†◊î ◊ß◊°◊ù..." : "◊¶◊ï◊® ◊ê◊™◊® ‚ú®"}
                    </motion.button>
                </div>
            </section>

            <div style={{
                display: 'grid',
                gridTemplateColumns: 'repeat(auto-fit, minmax(300px, 1fr))',
                gap: '24px',
                maxWidth: '1200px',
                margin: '0 auto'
            }}>
                {[
                    { title: '1. ◊û◊™◊ê◊®◊ô◊ù ◊ê◊™ ◊î◊¢◊°◊ß', desc: '◊õ◊ï◊™◊ë◊ô◊ù ◊û◊î ◊ê◊™◊ù ◊¢◊ï◊©◊ô◊ù, ◊ú◊û◊ô, ◊ï◊ê◊ô◊§◊î.' },
                    { title: '2. ◊î-AI ◊ë◊ï◊†◊î', desc: '◊ê◊†◊ó◊†◊ï ◊û◊ô◊ô◊¶◊®◊ô◊ù ◊¢◊ô◊¶◊ï◊ë ◊û◊ú◊ê ◊¢◊ù ◊ò◊ß◊°◊ò◊ô◊ù ◊©◊û◊ï◊õ◊®◊ô◊ù.' },
                    { title: '3. ◊û◊ï◊®◊ô◊ì◊ô◊ù ◊ï◊û◊©◊™◊û◊©◊ô◊ù', desc: '◊û◊ß◊ë◊ú◊ô◊ù ◊ß◊ë◊¶◊ô ZIP ◊û◊ï◊õ◊†◊ô◊ù ◊ú◊î◊¢◊ú◊ê◊î ◊ú◊ê◊ï◊ï◊ô◊®.' }
                ].map((f, i) => (
                    <div key={i} style={{
                        background: 'rgba(30, 41, 59, 0.3)',
                        padding: '40px',
                        borderRadius: '24px',
                        border: '1px solid rgba(255, 255, 255, 0.05)',
                        textAlign: 'center'
                    }}>
                        <h3 style={{ color: '#fff', marginBottom: '12px', fontSize: '1.5rem' }}>{f.title}</h3>
                        <p style={{ color: '#94a3b8', lineHeight: '1.6' }}>{f.desc}</p>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default function Home() {
    return (
        <Suspense fallback={<div>Loading...</div>}>
            <HomeContent />
        </Suspense>
    );
}
